/* eslint-disable @typescript-eslint/no-explicit-any */
import { ENV } from "@/lib/env";
import { logDebug, logError } from "@/lib/logger";

export const TecdocOps = {
  searchTerm: "articleSearchByTerm",
  byPlate: "getVehiclesByKeyNumberPlates",
  byVin: "getVehiclesByVIN",
  byMotorId: "getVehicleIdsByMotor2",
  manufacturers: "getManufacturers",
  models: "getModelSeries",
  types: "getVehicleIdsByCriteria",
  genericArticles: "getGenericArticles",
  amBrands: "getAmBrands",
  articleById: "getArticleDirectSearchById",
  articleImages: "getArticleImages",
  articleIdsWithState: "getArticleIdsWithState",
  assignedArticlesByIds6: "getAssignedArticlesByIds6",
} as const;

export type TecdocResource =
  | (typeof TecdocOps)[keyof typeof TecdocOps]
  | string;

export interface TecdocResponse<T = any> {
  status?: number;
  statusText?: string;
  data?: { array?: T[] } & Record<string, any>;
  [k: string]: any;
}

export async function tecdocCall<T = any>(
  resource: TecdocResource,
  params: Record<string, any> = {},
  init?: RequestInit
): Promise<TecdocResponse<T>> {
  const controller = new AbortController();
  const timeout = setTimeout(() => controller.abort(), 15000);

  // inner payload mirrors browser client examples
  const inner = {
    provider: Number(ENV.TECDOC_PROVIDER_ID),
    // many endpoints require these explicitly:
    lang: ENV.TECDOC_LANG_DEFAULT,
    country: ENV.TECDOC_LINKAGE_COUNTRY,
    countryCode: ENV.TECDOC_LINKAGE_COUNTRY,
    articleCountry: ENV.TECDOC_ARTICLE_COUNTRIES[0],
    ...params,
  };

  // top-level key = functionName/resource
  const body: Record<string, any> = { [resource]: inner };

  if (ENV.TECDOC_DEBUG)
    logDebug("REQUEST", { resource, url: ENV.TECDOC_BASE_JSON, body });

  try {
    const url = `${ENV.TECDOC_BASE_JSON}?api_key=${encodeURIComponent(
      ENV.TECDOC_API_KEY
    )}`;
    const res = await fetch(url, {
      method: "POST",
      headers: {
        "content-type": "application/json",
        "X-Api-Key": ENV.TECDOC_API_KEY,
      },
      body: JSON.stringify(body),
      signal: controller.signal,
      ...init,
    });

    clearTimeout(timeout);

    const text = await res.text();
    let json: TecdocResponse<T>;
    try {
      json = JSON.parse(text);
    } catch {
      json = { raw: text } as any;
    }

    if (!res.ok) {
      logError("RESPONSE", {
        status: res.status,
        statusText: res.statusText,
        raw: text.slice(0, 500),
      });
      throw new TecdocHttpError(
        String(resource),
        res.status,
        res.statusText,
        text
      );
    }

    if (ENV.TECDOC_DEBUG)
      logDebug("RESPONSE", {
        resource,
        status: json?.status,
        statusText: json?.statusText,
        dataLen: json?.data?.array?.length,
      });

    return json;
  } catch (err: any) {
    if (err?.name === "AbortError") {
      logError("TIMEOUT", { resource });
      throw new TecdocHttpError(
        String(resource),
        504,
        "Timeout",
        "Request timed out"
      );
    }
    logError("FETCH_ERROR", { resource, message: String(err?.message || err) });
    throw err;
  }
}

export class TecdocHttpError extends Error {
  constructor(
    public resource: string,
    public status: number,
    public statusText: string,
    public rawBody?: string
  ) {
    super(`TecDoc ${resource} ${status}: ${statusText}`);
  }
}

export function qp(req: Request, key: string, dflt?: string) {
  const url = new URL(req.url);
  return (url.searchParams.get(key) ?? dflt)?.trim();
}
export function qpn(req: Request, key: string, dflt?: number) {
  const v = qp(req, key);
  if (!v) return dflt;
  const n = Number(v);
  return Number.isFinite(n) ? n : dflt;
}
